@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
.card {
  cursor: pointer;
  margin: 10.5px 10.5px;
  max-width: 377px;
  min-width: 300px;
  flex: 1 1;
  height: auto;
  background-color: #F8F8F8;
  border: 1px solid #D8D8D8; }
  .card img {
    width: 100%;
    max-width: 377px; }
  .card__label {
    position: absolute;
    top: -35px;
    right: 0;
    background-color: #006F79;
    padding: 8px 11px 5px 10px;
    font-family: "Open Sans", sans-serif;
    font-size: 12px;
    font-weight: 700;
    color: #fff; }
    .card__label__overlay {
      position: relative;
      min-width: 300px; }
    .card__label.orange {
      background-color: #EC6608; }
  .card__description {
    margin: 0 0 0 20px;
    color: #363636; }
    .card__description__title {
      font-size: 20px;
      margin: 15px 0px 2px; }
    .card__description__text-one, .card__description__text-two {
      font-size: 16px;
      margin: 0px 0px 15px; }
    .card__description__text-small {
      font-size: 14px;
      margin: 0px 0px 15px; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400; }

.page {
  height: 100vh; }
  .page__title {
    color: #45852d;
    font-family: "Open Sans", sans-serif;
    font-weight: 700;
    font-size: 36px;
    text-align: center;
    padding: 50px 0px 36px; }
  .page__container {
    width: 100%;
    max-width: 1180px;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
    margin: 0 auto; }
  .page__button {
    margin: 39.5px 0px;
    display: flex;
    align-items: center;
    justify-content: center; }
    .page__button button {
      border: 1px solid #D8D8D8;
      background-color: #fff;
      padding: 12px 33px;
      border-radius: 40px;
      font-family: "Open Sans", sans-serif;
      font-weight: 700;
      outline: none;
      font-size: 16px;
      line-height: 24px; }
      .page__button button .button__icon {
        vertical-align: middle;
        margin-left: 10px; }

